@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
* {
  box-sizing: border-box;
  font-weight: 400;
}

:root {
  --black: black;
  --blue: #00acdc;
  --white: #fff;
  --dark-blue: #060b35;
  --gray: #616070;
}

body {
  background-color: var(--white);
  width: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__item {
  align-items: center;
  height: 100%;
  list-style-type: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav {
  align-items: center;
  height: 100%;
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0;
  display: flex;
}

.img {
  align-items: center;
  width: 40px;
  height: 40px;
}

.nav__link {
  color: var(--dark-blue);
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background: var(--blue);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

h2, p {
  margin: 0;
}

.main {
  grid-template-columns: repeat(var(--column-count), 200px);
  -moz-column-gap: 48px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  border: 1px solid var(--white);
  background: var(--white);
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px;
  font-weight: 400;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 3px 36px;
}

.card__title {
  color: var(--dark-blue);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.title {
  color: var(--gray);
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  margin: 16px 0 24px;
  display: flex;
}

.card__reviews {
  color: var(--dark-blue);
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.card__price-title {
  color: var(--gray);
  font-size: 12px;
  line-height: 18px;
}

.card__price-cost {
  color: var(--dark-blue);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  background: var(--blue);
  border: var(--blue);
  color: var(--white);
  text-align: center;
  border-color: var(--white);
  text-transform: uppercase;
  border-radius: 5px;
  height: 40px;
  margin: 16px 0 0;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__button:hover {
  color: var(--blue);
  border: 1px solid var(--blue);
  background: var(--white);
  border-radius: 5px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

